export default {
  name: 'StarZoom',
  props: {
    outWidth: {
      type: Number,
      default () {
        return 0
      }
    },
    outHeight: {
      type: Number,
      default () {
        return 0
      }
    },
    decimal: {
      type: Number,
      default () {
        return 0
      }
    }
  },
  computed: {
    innerWidth () {
      return this.decimal * this.outWidth
    }
  }
}
