export default {
  props: {
    index: {
      type: Number,
      default: 0
    },
    score: {
      type: Number,
      default: 0.5
    },
    size: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      start: false
    }
  },
  computed: {
    width () {
      const splitPoint = 2.5
      const max = 5
      const minWidth = 25
      if (this.score <= splitPoint) {
        return minWidth
      } else {
        const v = this.score / max * 100
        return v > minWidth ? v : minWidth
      }
    }
  },
  mounted () {
    setTimeout(() => {
      this.start = true
    }, 50)
  }
}
