import bar from '../../components/BarScore'
import Star from '../../components/common/starComp/StarZoom'
export default {
  name: 'Reviews',
  components: {
    bar,
    Star
  },
  props: {
    productDetailObj: {
      type: Object,
      default () {
        return {}
      }
    },
    tabsList: {
      type: Array,
      default () {
        return []
      }
    },
    analysisDimensionId: {
      type: String,
      default () {
        return null
      }
    },
    reviewsList: {
      type: Array,
      default () {
        return []
      }
    },
    disadvantageReviews: {
      type: Array,
      default () {
        return []
      }
    },
    advantageReviews: {
      type: Array,
      default () {
        return []
      }
    },
    currentPage: {
      type: Number,
      default () {
        return 1
      }
    },
    totalPage: {
      type: Number,
      default () {
        return 1
      }
    },
    // 优点总页数
    advantageReviewsTotalPage: {
      type: Number,
      default () {
        return 1
      }
    },
    // 缺点总页数
    disadvantageReviewsTotalPage: {
      type: Number,
      default () {
        return 1
      }
    },
    // 优点当前页
    advantageReviewsCurPage: {
      type: Number,
      default () {
        return 1
      }
    },
    // 缺点当前页
    disadvantageReviewsCurPage: {
      type: Number,
      default () {
        return 1
      }
    }
  },
  data () {
    return {
      allReviewsShow: true,
      partReviewsShow: false,
      advantageLen: 0,
      disadvantageLen: 0
    }
  },
  computed: {
    totalReviews () {
      return this.advantageLen + this.disadvantageLen
    }
  },
  methods: {
    contact () {
      this.$emit('formHandleShow')
    },
    changeTags (item) {
      this.allReviewsShow = false
      this.partReviewsShow = true
      this.advantageLen = parseInt(item.advantagenum)
      this.disadvantageLen = parseInt(item.disadvantagenum)
      this.$emit('changeTags', item)
    },
    handleGetAllReview () {
      if (this.allReviewsShow) {
        return
      }
      this.allReviewsShow = true
      this.partReviewsShow = false
      this.$emit('handleGetAllReview')
    },
    handleMoreReviews () {
      this.$emit('handleMoreReviews')
    },
    navigateToDetail (id) {
      let routeUrl = this.$router.resolve({
        name: 'review-detail-id',
        params: {
          id
        }
      })
      window.open(routeUrl.href, '_blank')
    },
    // 获取更多优点
    getMoreAdvantage () {
      this.$emit('getMoreAdvantage')
    },
    // 获取更多缺点
    getMoreDisadvantage () {
      this.$emit('getMoreDisadvantage')
    }
  }
}
